import React from "react";
import RoboCard from "./robocard";

import ID from "./robo-utils";

export default function RoboBaseGrid(props) {
	const gridData = props.configData.base;

	const listItems = gridData.map((el) => {

		return (
			<React.Fragment key={ID(el.type)}>
				<RoboCard
					title={el.title}
					type={el.type}
					selectGrid={props.selectGrid}
					clickCall={props.clickCall}
					imageUrl={props.imagePath}
					showNewLabel={ el.new ? true : false }
				/>
			</React.Fragment>
		);
	});
	
	return (
		<div className="d-flex justify-content-star flex-wrap">{listItems}</div>
	);
}
