import React from "react";
import RoboCard from "./robocard";
import { Scrollbars } from "react-custom-scrollbars";
import ID from "./robo-utils";


export default function RoboGrid(props) {
	const tabsData = props.configData.tabs;
	const tabsContentData = props.configData.tabsContent;	

	//console.log(props.premiumVersion)

	const TabContent = function(props) {
		if (props.hide) return null;


		var items = tabsContentData[props.tab];

		const listItems = items.map((el) => {
			return (
				<React.Fragment key={ID(el.type)}>
					<RoboCard
						title={el.title}
						hideTitle={false}
						type={el.type}
						image={el.image}
						selectGrid={props.selectGrid}
						clickCall={props.clickCall}
						colors={el.colors}
						imageUrl={props.imagePath}
					/>
				</React.Fragment>
			);
		});
		return <React.Fragment>{listItems}</React.Fragment>;
	};

	const listTabs = tabsData.map((tab) => {
			
		return (
			<React.Fragment key={ID(tab)}>
				<TabContent
					tab={tab}
					hide={props.currentTab === tab ? false : true}
					{...props}
				/>
			</React.Fragment>
		);
	});

	return (
		<Scrollbars		 	
			style={{
				height: 220,
			}}
		>
			<div className="d-flex justify-content-star flex-wrap" style={{ paddingLeft: '1rem' }}>
				{listTabs}
			</div>
		</Scrollbars>
	);

}
