import React from "react";
import { Scrollbars } from "react-custom-scrollbars";
import RoboCard from "./robocard";
import ID from "./robo-utils";

export default function RoboSlider(props) {
	const cards = props.configData.slider;

	
    

	const listItems = cards.map((el) => (
		<React.Fragment key={ID(el.type)}>
			<RoboCard
				title={el.title}
				selectGrid={props.currentTab}
				type={el.type}
				clickCall={props.clickCall}
				currentTab={props.currentTab}
				small={true}
				medium={true}
				imageUrl={props.imagePath}
			/>
		</React.Fragment>
	));

	return (
		<Scrollbars
			style={{
				width: 90,
				height: 220,
				borderRight: "1px solid rgba( 0, 0, 0, 0.1)",
			}}
		>
			{listItems}
		</Scrollbars>
	);
}
