import React from 'react';
import RoboDialog from './components/modal';

let imageUrl = 'grids/';
let premiumVersion = false;
let showDialog = false;

if(window.robo_js_config !== undefined){
  imageUrl = window.robo_js_config.imagesUrl + 'grids/';

  premiumVersion = 	window.robo_js_config.premiumVersion===1 || 
  					window.robo_js_config.premiumVersion==="1" || 
  					window.robo_js_config.premiumVersion===true;
  
  showDialog = 	window.robo_js_config.showDialog===1 || 
  				window.robo_js_config.showDialog==="1" || 
  				window.robo_js_config.showDialog===true;

  //console.log('premiumVersion', window.robo_js_config.premiumVersion);
  //console.log('showDialog', window.robo_js_config.showDialog);
}

function App() {
  return (
    <React.Fragment>
      <RoboDialog imageUrl={imageUrl} premiumVersion={premiumVersion} showDialog={showDialog} />
    </React.Fragment>
  );
}

export default App;
