import React from "react";
import Card from "react-bootstrap/Card";
/*import ID from "./robo-utils";*/

const cardStyle = {
	card: {
		width: "6rem",
		marginTop: "0.6rem",
		marginBottom: "0",
		marginLeft: 0,
		marginRight: "1.6rem",
		border: "0 none",
		cursor: "pointer",
		padding: "0",
		boxShadow: 'none',

		body: {
			padding: "0.35rem",
		},
		title: {
			marginBottom: "0.75rem",
		},

		imagesize:{
			width: "96px", 
			height: "96px"
		}
	},
	smallcard: {
		width: "2.5rem",
		padding: "0",
		boxShadow: 'none',
		
		marginTop: "0.6rem",
		marginBottom: "0",
		marginLeft: 0,
		marginRight: "1.2rem",

		border: "0 none",
		cursor: "pointer",
		body: {
			padding: "0.35rem",
		},
		title: {
			marginBottom: "0.55rem",
			fontSize: "0.75rem",
		},
		imagesize:{
			width: "40px", 
			height: "40px"
		}
	},
	fildercard: {
		filter: "grayscale(1)",  // blur(2px)
	}
};

export default function RoboCard( props ) {
	var styleCur = props.small ? cardStyle.smallcard : cardStyle.card;
	
	if( props.medium ){
		styleCur = {
		...cardStyle.smallcard ,
		...props.style,
		width:  "4rem",
		imagesize:{
			width: "64px", 
			height: "64px"
		}
		};
	}


	const Title = function(props) {
		if (props.hideTitle === true) return null;
		return (
			<Card.Title className="text-center" as="h6" style={styleCur.title}>
				{props.title}
			</Card.Title>
		);
	};

	const LabelNew = function(props) {
		if ( !props.showNewLabel ) return null;
		return (
			<span className="robo-newdialog-label-new">
				NEW
			</span>
		);
	};

	const grayscaleCur = props.grayscale ? cardStyle.fildercard : {  };	

	return (
		<Card
			style={styleCur}
			type={props.type}
			onClick={(event) => { props.clickCall( props.type, event ) }}
		>
			<LabelNew showNewLabel={props.showNewLabel} />
			<Card.Img
				variant="top"
				src={
					props.imageUrl +
					( props.image ? props.image : props.type ) +
					(props.selectGrid === props.type ? "_active" : "") +
					".svg"
				}
				style={{ ...grayscaleCur, ...styleCur.imagesize }}
			/>

			<Card.Body style={styleCur.body}>
				<Title title={props.title} hideTitle={props.hideTitle} />
			</Card.Body>
		</Card>
	);
}
